@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Icon {
  fill: currentColor;
  transform-origin: center center;
}

.Icon--direction-s {
  transform: rotate(90deg);
}
.Icon--direction-w {
  transform: rotate(180deg);
}
.Icon--direction-n {
  transform: rotate(-90deg);
}
.Icon--direction-ne {
  transform: rotate(-45deg);
}

.NetworkSearch {
  position: relative;
}
.NetworkSearch__input {
  position: relative;
  z-index: 30;
}
.NetworkSearch__input input {
  background: var(--gray-lightest);
  border: 1px solid var(--gray-light);
  border: none;
  width: 100%;
  font-size: 0.9em;
  padding: 0.5em 0.8em;
  padding-right: 1.9em;
  font-weight: 100;
}
.NetworkSearch__input input:placeholder {
  font-weight: 100;
}
.NetworkSearch__input__close {
  position: absolute;
  right: 0.6em;
  top: 50%;
  color: var(--gray);
  transform: translateY(-50%);
  cursor: pointer;
}
.NetworkSearch__modal {
  /* position: absolute;
  bottom: 0;
  left: 0; */
  /* transform: translate(0, 100%); */
  position: fixed;
  top: 15.5em;
  left: 19.6em;
  width: 19rem;
  max-height: calc(100vh - 10rem);
  font-size: 0.8em;
  color: var(--gray-dark);
  background: var(--gray-lightest);
  padding: 0 1em 1em;
  overflow: auto;
  z-index: 100;
}
.NetworkSearch__modal__background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  opacity: 0.5;
  z-index: 20;
  cursor: pointer;
}
.NetworkSearch__modal h6 {
  margin: 1em 0 0.3em;
}
.NetworkSearch__options {
  display: flex;
  flex-wrap: wrap;
}
.NetworkSearch__option {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 2px solid #fff;
  outline: none;
  background: #fff;
  /* color: white; */
  padding: 0.6em 1em;
  margin: 0 0.3em 0.3em 0;
  white-space: nowrap;
  max-width: 20em;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.NetworkSearch__option:hover,
.NetworkSearch__option:focus {
  border-color: var(--accent-light);
}

.NetworkBubbles {
  position: relative;
  /* display: flex; */
  width: 100%;
  height: 100%;
  /* max-height: 56em; */
  margin: 0 auto;
}
.NetworkBubbles__wrapper {
  position: relative;
}
.NetworkBubbles__drag-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: move;
  cursor: -webkit-grab;
}
.NetworkBubbles__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.NetworkBubbles__overlay path,
.NetworkBubbles__overlay text {
  transition: all 0.3s;
}
.NetworkBubbles__overlay rect {
  pointer-events: all;
}
.NetworkBubbles__controls {
  position: fixed;
  bottom: 2em;
  right: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.NetworkBubbles__control-row {
  display: flex;
  align-items: center;
  margin: -0.4em;
}
.NetworkBubbles__control-row .NetworkBubbles__control {
  margin: 0 0.6em;
}
.NetworkBubbles__control {
  font-size: 1em;
  font-weight: 600;
  width: 2em;
  height: 2em;
  margin: 0.2em 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.4em;
  background: #fff;
  border-radius: 100%;
}

.NetworkBubbles text {
  font-weight: 700;
  font-size: calc(0.4em + 0.2vw + 0.2vh);
  fill: var(--text-color);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.NetworkBubbles circle {
  /* fill: #30336b; */
  transition: all 0.6s ease-out;
}
.NetworkBubbles__link {
  fill: none;
  stroke: var(--accent-medium);
  stroke: var(--gray);
  stroke-width: 1;
  pointer-events: none;
}
.NetworkBubbles__link--equal {
  stroke: var(--gray);
}
.NetworkBubbles__arrow {
  fill: var(--accent-medium);
  fill: var(--gray);
}
@-webkit-keyframes pulse {
  0% {
    stroke-dashoffset: 0%;
  }
  100% {
    stroke-dashoffset: 100%;
  }
}
@keyframes pulse {
  0% {
    stroke-dashoffset: 0%;
  }
  100% {
    stroke-dashoffset: 100%;
  }
}
.NetworkBubbles__link__pulse {
  fill: none;
  stroke: var(--accent-medium);
  stroke: #fff;
  stroke-width: 2.7;
  stroke-dasharray: 1 9;
  pointer-events: none;
  -webkit-animation: pulse 53s linear;
          animation: pulse 53s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-opacity: 0.7;
  mix-blend-mode: color-dodge;
}
.NetworkBubbles .NetworkBubbles__group {
  fill: #000;
}
.NetworkBubbles__cluster {
  fill: #fff;
  stroke: #fff;
  stroke-linejoin: round;
}
.NetworkBubbles .NetworkBubbles__cluster-name {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 0.8em;
  color: var(--gray-dark);
  text-transform: uppercase;
  letter-spacing: 0.16em;
  font-weight: 700;
  text-anchor: middle;
}
.NetworkBubbles__mouse-target {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 100%;
  cursor: pointer;
}
.NetworkBubbles__type-use {
  opacity: 0;
  transition: all 0.2s ease-out;
}
.NetworkBubbles__group-g {
  cursor: pointer;
  transition: all 0.2s ease-out;
}
.NetworkBubbles__group-g--small:hover .NetworkBubbles__type-use {
  opacity: 1;
}
.NetworkBubbles__mission {
  cursor: pointer;
  transition: all 0.2s ease-out;
}
.NetworkBubbles__mission:hover .NetworkBubbles__type-use {
  opacity: 1;
}
.NetworkBubbles--is-hovering .NetworkBubbles__mission {
  opacity: 0.1;
}
.NetworkBubbles--is-hovering .NetworkBubbles__mission--is-hovered--status,
.NetworkBubbles--is-hovering .NetworkBubbles__mission--is-hovered--type,
.NetworkBubbles--is-hovering .NetworkBubbles__mission--is-hovered--search,
.NetworkBubbles__mission--is-hovered--type .NetworkBubbles__type-use {
  opacity: 1;
}
.NetworkBubbles__annotation-text {
  position: absolute;
  top: 0;
  left: 0;
  overflow: visible;
  pointer-events: none;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.NetworkBubbles__annotation {
  position: absolute;
  top: -6em;
  left: -6em;
  font-size: 0.8em;
  pointer-events: none;
  -webkit-animation: fadeOut 0.3s ease-out;
          animation: fadeOut 0.3s ease-out;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.NetworkBubbles__annotation-line {
  width: 6em;
  height: 6em;
  fill: none;
  stroke: white;
  stroke-width: 1;
}
.NetworkBubbles__annotation-text {
  position: absolute;
  top: -0.3em;
  left: 0;
  width: 10em;
  line-height: 1.3em;
  text-align: right;
  transform: translate(-80%, -100%);
  color: white;
}
.NetworkBubbles i {
  font-style: italic;
}

.NetworkFilters {
}
.NetworkFilters h6 {
  margin: 0 0 0.3em;
  font-size: 0.8em;
  line-height: 1.3em;
  font-weight: 700;
}
.NetworkFilters__filter {
  margin-top: 1.3em;
}
.NetworkFilters__filter__item {
  /* position: relative; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.8em;
  line-height: 1.3em;
  padding: 0.4em 0.6em;
  margin-left: -0.6em;
  width: calc(100% + 1.2em);
  cursor: pointer;
  color: var(--override-color);
}
.NetworkFilters__filter__item__info {
  position: absolute;
  /* bottom: 0; */
  padding: 1em;
  max-width: min(37em, 90vw);
  color: var(--text-color);
  background: #fff;
  pointer-events: none;
  transform: translateY(50%);
  margin-top: 2.6em;
  margin-left: -1em;
  opacity: 0;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  z-index: 10;
}
.NetworkFilters__filter__item__info b {
  color: var(--override-color);
}
.NetworkFilters__filter__item:hover .NetworkFilters__filter__item__info {
  opacity: 1;
}
.NetworkFilters__filter__item .Icon {
  flex: 0 0 12px;
  color: #fff;
}
.NetworkFilters__filter__item--is-unselected {
  color: var(--gray);
}
.NetworkFilters__filter__item--is-selected {
  background: var(--accent-3);
  background: var(--override-color, var(--accent-3));
  color: #fff;
}
.NetworkFilters__filter__item:hover {
  background: var(--accent-lightest);
}

.NetworkFilters__filter__item--is-selected:hover {
  background: var(--accent-3);
  background: var(--override-color, var(--accent-3));
}

@media (max-width: 900px) {
  .NetworkFilters__filter__item__info {
    display: none;
  }
}

.NetworkList {
}
.NetworkList h6 {
  margin: 0 0 0.3em;
  font-size: 0.8em;
  line-height: 1.3em;
  font-weight: 700;
}
.NetworkList__select {
  width: 100%;
  margin-bottom: 1.3em;
  padding: 0.5em 0.6em;
}
.NetworkList__filter {
  position: relative;
}
.NetworkList__clear {
  position: absolute;
  top: 0;
  right: 0.2em;
  font-size: 0.5em;
  color: var(--gray);
}
.NetworkList__filter__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.8em;
  line-height: 1.3em;
  padding: 0.4em 0.6em;
  margin-left: -0.6em;
  width: calc(100% + 1.2em);
  cursor: pointer;
}
.NetworkList__filter__item .Icon {
  flex: 0 0 12px;
  color: #fff;
}
.NetworkList__filter__item--is-unselected {
  /* color: var(--gray); */
}
.NetworkList__filter__item--is-selected {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  bottom: 0;
  background: var(--accent-3);
  color: #fff;
  z-index: 10;
}
.NetworkList__filter__item:hover {
  background: var(--accent-lightest);
}

.NetworkList__filter__item--is-selected:hover {
  background: var(--accent-2);
}

.NetworkTooltip {
  position: fixed;
  top: 2em;
  right: 2em;
  width: min(20em, 90vw);
  font-size: 0.9em;
  line-height: 1.3em;
  text-align: right;
}
.NetworkTooltip__name {
  /* display: flex;
  justify-content: space-between; */
  width: 100%;
  font-weight: 800;
}
.NetworkTooltip__text {
  margin: 0.3em 0;
  font-size: 0.9em;
  line-height: 1.3em;
}
.NetworkTooltip__year {
  margin-left: 1em;
  font-weight: 500;
  font-size: 0.8em;
  opacity: 0.6;
  white-space: nowrap;
}
.NetworkTooltip img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  max-height: 30vh;
  transform: translate(0, -100%);
}
.NetworkTooltip__type-name {
  text-transform: uppercase;
  letter-spacing: 0.06em;
  font-size: 0.9em;
  font-weight: 700;
  margin-left: 0.9em;
}
.NetworkTooltip__info {
  font-size: 0.9em;
  line-height: 1.4em;
  margin: 0.3em 0;
}
.NetworkTooltip__note {
  font-size: 0.9em;
  font-style: italic;
  opacity: 0.6;
  line-height: 1.4em;
  margin: 0.3em 0;
}

@media (max-width: 850px) {
  .NetworkTooltip {
    top: auto;
    bottom: 5em;
    left: 2em;
    text-align: left;
  }
}

.StatusPill {
  display: inline-block;
  padding: 0.5em 0.8em;
  background: var(--status-color);
  color: var(--status-color);
  border-radius: 0.2em;
  line-height: 1em;
  font-weight: 600;
}

.NetworkModal {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 500;
  pointer-events: none;
}

.NetworkModal h6 {
  margin-bottom: 0.3em;
}

.NetworkModal__content {
  min-width: 20em;
  max-width: 80vw;
  max-height: 80vh;
  padding: 2em;
  background: white;
  color: var(--accent-dark);
  overflow: auto;
  pointer-events: all;
}

.NetworkModal__background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: var(--accent-dark);
  opacity: 0.8;
  cursor: pointer;
  z-index: 100;
}

.NetworkModal__close {
  position: absolute;
  top: 1em;
  right: 1.4em;
  color: var(--gray);
  cursor: pointer;
}

.NetworkModal__status {
  display: flex;
  align-items: center;
  margin-bottom: 0.5em;
}
.NetworkModal__status__info {
  padding-left: 0.8em;
  font-size: 0.8em;
  line-height: 1.3em;
  /* color: var(--gray); */
  font-style: italic;
}
.NetworkModal__name {
  font-size: 2em;
  line-height: 1.27em;
  margin-bottom: 0.5em;
  font-weight: 800;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  font-weight: 800;
}
.NetworkModal__year {
  margin-left: 1.7em;
  font-weight: 500;
  font-size: 0.6em;
  opacity: 0.6;
  white-space: nowrap;
}
.NetworkModal__status--Active,
.NetworkModal__status--Planned {
  color: var(--accent-light);
}
.NetworkModal__label {
  font-size: 0.9em;
  font-weight: 600;
}
.NetworkModal img {
  position: absolute;
  top: 50%;
  right: 1em;
  max-width: 100%;
  transform: translate(100%, -50%);
  max-width: 15em;
}
.NetworkModal__type-name {
  margin-top: 1em;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  font-size: 0.9em;
  font-weight: 700;
  margin-right: 1em;
}
.NetworkModal__info {
  /* font-size: 0.9em; */
  /* line-height: 1.4em; */
  margin: 1.3em 0;
}
.NetworkModal__actor {
  padding-bottom: 2em;
  font-size: 1.1em;
  line-height: 1.4em;
  font-weight: 600;
}
.NetworkModal__cards {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -0.2em;
  font-size: 0.9em;
}
.NetworkModal__card {
  max-width: 35em;
  margin: 0.2em;
  padding: 0.4em 1em;
  background: #f4f4f4;
  border-radius: 0.3em;
}
.NetworkModal__text {
  line-height: 1.6em;
  margin: 0.3em 0;
}

@media (max-width: 800px) {
