    width: 100%;
    max-width: calc(100vw - 2em);
  }
  .NetworkModal__content {
    min-width: 0;
    max-width: calc(100vw - 2em);
  }
  .NetworkModal img {
    position: relative;
    top: 0;
    right: 0;
    max-width: 100%;
    transform: none;
    max-width: 15em;
  }
}
@media (max-height: 700px) {
  .NetworkModal {
    height: 100%;
    max-height: calc(100vh - 2em);
  }
  .NetworkModal__content {
    min-height: 0;
    max-height: calc(100vh - 2em);
  }
}

.Network__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.Network__main {
  width: 100%;
  display: flex;
  height: 100%;
}
.Network__bubbles {
  width: 100%;
  position: relative;
  flex: 1 1;
  min-width: 30em;
  /* padding-left: 12em; */
}
.Network__about,
.Network__sidebar__close,
.Network__mobile-buttons {
  display: none;
}
.Network__info {
  position: relative;
  width: min(90vw, 35em);
  font-size: 0.86em;
  line-height: 1.5em;
  margin: -0.2em 0 2em;
  pointer-events: none;
  z-index: 10;
}
.Network__sidebar {
