.Network__sidebar h6 {
  margin-bottom: 0.3em;
}

.Network__sidebar__section {
  padding: 1em;
}
.Network__sidebar__section + .Network__sidebar__section {
  padding-top: 0;
  /* border-top: 1px solid #e2e8ee; */
}
.Network__button {
  display: block;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  border: none;
