  margin-bottom: 3em;
}

.Network__toggle {
  margin: -0.5em 0 0.5em;
}
.Network__toggle__button {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0.2em 0.3em;
  margin: 0;
  font-size: 0.8em;
  background: none;
  cursor: pointer;
}
.Network__toggle__button--is-selected {
