@media (max-width: 850px), (max-height: 600px) {
  .Network__sidebar__top {
    z-index: 10;
  }
  .Network__type {
    width: 10em;
  }
  .Network__sidebar__bottom-label {
    display: none;
  }
  .Network__sidebar__bottom {
    position: fixed;
    top: 1em;
    right: 1em;
    bottom: 1em;
    left: 1em;
    transform: translate(-100vw);
    z-index: 100;
    transition: transform 0.3s ease-out;
  }
  .Network__about {
    display: block;
    position: fixed;
    top: 1em;
    right: 1em;
    bottom: 1em;
    left: 1em;
    padding: 2em 1.6em;
    background: #fff;
    transform: translate(-100vw);
    overflow: auto;
    z-index: 100;
    transition: transform 0.3s ease-out;
  }
  .Network__sidebar {
    position: absolute;
  }
  .Network__sidebar__close {
    display: block;
    position: fixed;
    bottom: 2em;
    right: 2em;
    z-index: 100;
  }

  .Network__info {
    display: none;
  }

  .Network__mobile-buttons {
    position: fixed;
    right: 1em;
    bottom: 1em;
    display: flex;
    z-index: 10;
  }
  .Network__mobile-button + .Network__mobile-button {
    margin-left: 0.5em;
  }
  .Network__sidebar--filters-showing .Network__sidebar__bottom {
    transform: none;
  }
  .Network__sidebar--about-showing .Network__about {
    transform: none;
  }
}
@media (max-height: 700px) {
  .Network__wrapper .Network__controls {
    font-size: 0.9em;
  }
  .Network__wrapper .NetworkTimeline {
    font-size: 0.9em;
  }
  .Network__wrapper .NetworkLegend__types {
    display: none;
  }
}
@media (max-width: 650px) and (max-height: 800px) {
  .Network__wrapper .Network__title {
    display: none;
  }
  .Network__sidebar {
    margin-left: 0;
  }
  .Network__bubbles {
    min-width: 90vw;
  }
}
@media (max-height: 600px) {
  .Network__wrapper .Network__title {
    display: none;
  }
  .Network__wrapper .Network__controls {
    font-size: 0.8em;
    margin-top: 0.3em;
    margin-bottom: 0.3em;
  }
  .Network__wrapper .Network__timeline {
    margin-bottom: 0.6em;
  }
  .Network__wrapper .NetworkTimeline {
    font-size: 0.8em;
  }
  .Network__wrapper .NetworkBubbles {
    height: calc(100vh - 3em);
  }
  .Network__wrapper .Network__search {
    top: 6em;
  }
  .Network__wrapper .NetworkLegend {
    top: 9em;
  }
}
@media (max-height: 500px) {
