    display: none;
  }
}

.MapTooltip {
  position: fixed;
  top: 1em;
  right: 1em;
  padding: 1em;
  width: min(20em, 90vw);
  font-size: 0.9em;
  line-height: 1.3em;
  text-align: right;
  background: var(--gray-lightest);
  z-index: 10;
}
.MapTooltip__name {
  /* display: flex;
  justify-content: space-between; */
  width: 100%;
  font-weight: 800;
}
.MapTooltip__text {
  margin: 0.3em 0;
  font-size: 0.9em;
  line-height: 1.3em;
}
.MapTooltip__year {
  margin-left: 1em;
  font-weight: 500;
  font-size: 0.8em;
  opacity: 0.6;
  white-space: nowrap;
}
.MapTooltip img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  max-height: 30vh;
  transform: translate(0, -100%);
}
.MapTooltip__type-name {
  text-transform: uppercase;
  letter-spacing: 0.06em;
  font-size: 0.9em;
  font-weight: 700;
  margin-left: 0.9em;
}
.MapTooltip__info {
  font-size: 0.9em;
  line-height: 1.4em;
  margin: 0.3em 0;
}
.MapTooltip__note {
  font-size: 0.9em;
  font-style: italic;
  opacity: 0.6;
  line-height: 1.4em;
  margin: 0.3em 0;
}

@media (max-width: 850px) {
  .MapTooltip {
    top: auto;
    bottom: 5em;
    left: 2em;
    text-align: left;
  }
}

.Globe {
  width: 100%;
  max-height: 95vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.Globe__controls {
}

.Map {
  width: 100%;
  height: 95vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.Map__controls {
}

.MapWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.MapWrapper__controls {
  flex: 0 0 15em;
  max-width: 15em;
  margin: 1em;
  display: flex;
  flex-direction: column;
  max-height: 90vh;
  padding-top: 1em;
  padding-left: 0.5em;
}

.MapWrapper__sidebar {
  padding: 0 1em 1em;
  background: #fff;
  overflow: auto;
}

.MapWrapper__sidebar__bottom-label {
  margin-top: 1.3em;
  margin-left: 1em;
  margin-bottom: 0;
}

.MapWrapper__main {
  flex: 1 1;
  overflow: hidden;
}

.Timeline {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.Timeline__wrapper {
  position: relative;
  width: 100%;
  flex: 1 1;
  overflow: hidden;
}
.Timeline__svg {
  margin-top: 3px;
  overflow: visible;
}
.Timeline__line {
  fill: none;
  stroke-width: 2;
  stroke-linecap: round;
  stroke: var(--accent-2);
}
.Timeline__area {
  fill: var(--accent-2);
  opacity: 0.2;
}
.Timeline__uncertainty-area {
  fill: var(--accent-2);
  opacity: 0.3;
}
.Timeline__x-ticks {
  position: absolute;
  bottom: 0;
  left: 0;
}
.Timeline__x-tick {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 0.3em;
  border-right: 1px solid var(--accent-2);
  z-index: 10px;
}
.Timeline__x {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.9em;
}

.Timeline__delta {
  opacity: 0.8;
}

.Benchmarks {
  padding: 2em;
  width: 100%;
  height: 100%;
}
.Benchmarks__grid {
  display: grid;
  height: calc(100% - 7em);
  grid-template-columns: repeat(3, minmax(100px, 1fr));
  grid-template-rows: repeat(2, minmax(100px, 1fr));
  grid-gap: 0.6em;
  grid-column-gap: 1em;
}
.Benchmarks h1 {
  position: relative;
  margin-top: 0;
  margin-bottom: 0.9em;
  font-size: 2em;
  line-height: 1em;
  width: min(90vw, 20em);
  font-weight: 800;
  pointer-events: none;
  z-index: 10;
}
.Benchmarks h2 {
  position: relative;
  margin-top: 0;
  font-size: 1.3em;
  line-height: 1em;
  font-weight: 800;
}
.Benchmarks__card {
  background: white;
  padding: 1em 1.6em;
  max-height: 50vh;
  display: flex;
  flex-direction: column;
}
.Benchmarks__card:nth-of-type(2) {
  --accent-2: var(--accent-3);
}
.Benchmarks__card:nth-of-type(3) {
  --accent-2: var(--accent-4);
}
.Benchmarks__card:nth-of-type(4) {
  --accent-2: #bc8797;
}
.Benchmarks__card--metrics {
  padding-top: 2em;
}
.Benchmarks__card__top {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  margin-top: 0.5em;
  margin-bottom: 1em;
}
.Benchmarks__card__top__value {
  text-align: right;
}
.Benchmarks__value {
  font-size: 2em;
  white-space: nowrap;
  font-weight: 300;
  color: var(--accent-2);
  font-feature-settings: "tnum" 1;
}
.Benchmarks__chart {
  flex: 1 1;
  min-height: 6em;
  margin-bottom: 1em;
  overflow: hidden;
}
.Benchmarks__fact {
  /* font-style: italic; */
  min-height: 5em;
  /* display: flex;
  align-items: center; */
  font-size: 0.8em;
  line-height: 1.3em;
  /* color: var(--accent-2); */
  color: #276796;
  /* font-weight: 450; */
  background: var(--gray-lightest);
  /* color: var(--gray-darker); */
  margin: 0 -1.6rem -1rem -1.6rem;
  padding: 1em 1.6rem 0 1.6rem;
}
.Benchmarks__note {
  display: flex;
  margin: auto 0 0.5em 0;
  color: var(--gray-darker);
  align-items: baseline;
  justify-content: space-between;
  padding-bottom: 0.6em;
  font-style: italic;
  font-size: 0.8em;
  line-height: 1.3em;
}
.Benchmarks__note > div + div {
  text-align: right;
}

.Benchmarks__note a,
.Benchmarks__note a:visited,
.Benchmarks__note a:hover {
  color: var(--accent-2);
}
.Benchmarks__metric-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  line-height: 1.5em;
  padding: 1em 0;
}

@media (max-width: 1550px) {
  .Benchmarks__card__top {
    flex-direction: column;
  }
}
@media (max-width: 1300px) {
  .Benchmarks h2 {
    font-size: 1.1em;
    font-weight: 600;
    margin-bottom: 0.2em;
  }
  .Benchmarks__metric-row {
    flex-direction: column;
    align-items: flex-start;
  }
  .Benchmarks__value {
    font-size: 1.3em;
    font-weight: 500;
  }
  .Benchmarks__metric-row .Benchmarks__value {
    margin-top: 0.3em;
  }
  .Benchmarks__note a {
    margin-top: 0.6em;
  }
}

@media (max-width: 850px) {
  .Benchmarks {
    overflow: auto;
  }
  .Benchmarks__grid {
    grid-template-columns: repeat(2, minmax(100px, 1fr));
    grid-template-rows: 1fr 1fr 2fr;
    height: auto;
  }
  .Benchmarks__fact {
    display: none;
  }
  .Benchmarks__chart {
    height: 13em;
    min-height: 13em;
    max-height: 13em;
  }
}
@media (max-width: 580px) {
  .Benchmarks__grid {
    display: flex;
    flex-direction: column;
    padding-bottom: 3em;
  }
}

html {
  --accent-darker: #000;
  --accent-dark: #0d0c12;
  --text-color: var(--accent-dark);
  --accent-medium: #7ca4c3;
  --accent-1: #afb9c5;
  --accent-2: #255445;
  --accent-2: #008b94;
  --accent-3: #729486;
  --accent-3: #5da17c;
  --accent-4: #866fac;
  /* --accent-1: #bc8797; */
  --accent-light: #efd1c9;
  --accent-lighter: #d3dae2;
  --accent-lightest: #e2e8ee;
  --gray: #a8a4a4;
  --gray-light: #ddd9d9;
  --gray-lighter: #d6dfe8;
  --gray-lightest: #e2e8f0;
  --gray-dark: #d4b46b;
  /* --gray-dark: #888; */
  --gray-dark: #afb9c5;
  --gray-darker: #6f7985;

  font-family: Inter, serif;
  font-size: 1.1em;
  line-height: 1.6em;
  font-weight: 400;
  color: #0d0c12;
  color: var(--text-color);
  background: #e2e8ee;
  background: var(--accent-lightest);
  background: #e2e8f0;
  background: var(--gray-lightest);
  /* background: #130f40; */
  /* background: #313f53; */
}

body {
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

h1 {
  font-family: var(--heading-font);
  font-size: 5rem;
  line-height: 1.3em;
  font-weight: 500;
}

h2 {
  font-family: var(--heading-font);
  font-size: 2.6em;
  line-height: 1.5em;
  font-weight: 500;
  margin-bottom: 0.6em;
}

h6 {
  font-size: 0.8em;
  text-transform: uppercase;
  font-family: var(--figure-font);
  letter-spacing: 0.1em;
  font-weight: 500;
  margin-bottom: 1em;
}

strong {
  font-weight: 900;
}

p button {
  font-family: Georgia, serif;
}

button,
figure,
svg {
  font-family: var(--figure-font);
}

button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  margin-top: 0.4em;
  padding: 0.56em 1em;
  background: #fff;
  font-size: 0.8em;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.09em;
  border: 1px solid transparent;
  box-shadow: none;
  cursor: pointer;
}

a {
  color: #008b94;
  color: var(--accent-2);
}
a:visited {
  color: #5da17c;
  color: var(--accent-3);
}

::selection {
  background: #efd1c9;
  background: var(--accent-light);
}

html,
body,
#root {
  height: 100%;
}
