@import url("https://rsms.me/inter/inter.css");
html {
  --accent-darker: #000;
  --accent-dark: #0d0c12;
  --text-color: var(--accent-dark);
  --accent-medium: #7ca4c3;
  --accent-1: #afb9c5;
  --accent-2: #255445;
  --accent-2: #008b94;
  --accent-3: #729486;
  --accent-3: #5da17c;
  --accent-4: #866fac;
  /* --accent-1: #bc8797; */
  --accent-light: #efd1c9;
  --accent-lighter: #d3dae2;
  --accent-lightest: #e2e8ee;
  --gray: #a8a4a4;
  --gray-light: #ddd9d9;
  --gray-lighter: #d6dfe8;
  --gray-lightest: #e2e8f0;
  --gray-dark: #d4b46b;
  /* --gray-dark: #888; */
  --gray-dark: #afb9c5;
  --gray-darker: #6f7985;

  font-family: Inter, serif;
  font-size: 1.1em;
  line-height: 1.6em;
  font-weight: 400;
  color: var(--text-color);
  background: var(--accent-lightest);
  background: var(--gray-lightest);
  /* background: #130f40; */
  /* background: #313f53; */
}

body {
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

h1 {
  font-family: var(--heading-font);
  font-size: 5rem;
  line-height: 1.3em;
  font-weight: 500;
}

h2 {
  font-family: var(--heading-font);
  font-size: 2.6em;
  line-height: 1.5em;
  font-weight: 500;
  margin-bottom: 0.6em;
}

h6 {
  font-size: 0.8em;
  text-transform: uppercase;
  font-family: var(--figure-font);
  letter-spacing: 0.1em;
  font-weight: 500;
  margin-bottom: 1em;
}

strong {
  font-weight: 900;
}

p button {
  font-family: Georgia, serif;
}

button,
figure,
svg {
  font-family: var(--figure-font);
}

button {
  appearance: none;
  margin-top: 0.4em;
  padding: 0.56em 1em;
  background: #fff;
  font-size: 0.8em;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.09em;
  border: 1px solid transparent;
  box-shadow: none;
  cursor: pointer;
}

a {
  color: var(--accent-2);
}
a:visited {
  color: var(--accent-3);
}

::selection {
  background: var(--accent-light);
}

html,
body,
#root {
  height: 100%;
}
.App {
  height: 100%;
  /* padding: 1em; */
}
.App__loading {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90vh;
}
.App__error {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90vh;
}

::-moz-scrollbar {
  width: 9px;
}
::-webkit-scrollbar {
  width: 9px;
}

::-moz-scrollbar-track {
  box-shadow: none;
  border-radius: 10px;
  background: #fff;
}
::-webkit-scrollbar-track {
  box-shadow: none;
  border-radius: 10px;
  background: #fff;
}
::scrollbar-thumb {
  border-radius: 10px;
  background: #dad6d6;
  box-shadow: none;
}
::-moz-scrollbar-thumb {
  background: #dad6d6;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb {
  background: #dad6d6;
  border-radius: 6px;
}

.App__nav {
  display: flex;
  margin-left: 1.5em;
  position: absolute;
  bottom: 1.3em;
  z-index: 10;
}
.App__nav a {
  margin-right: 0.8em;
  padding: 0.4em 1em;
  border-radius: 0.6em;
  text-decoration: none;
  font-weight: 600;
}
.App__nav a.active {
  color: var(--accent-2);
  background: white;
}
.App__nav a.inactive {
  color: var(--accent-dark);
  background: var(--gray-lighter);
}
